.Project {
    display: flex;
    margin-bottom: 60px;
}

.Left {
    width: 50%;
    display: flex;
    justify-content: center;
}

.Left img {
    width: 90%;
    margin: 0 auto;
}

.Right {
    width: 100%;
    padding-top: 20px;
}

.AppName {
    text-align: center;
    font-size: 22px;
    font-weight: 400;
    text-transform: capitalize;
}

.Explain {
    padding: 20px 0;
}

.Links {
    display: flex;
    justify-content: center;
}

.Links a {
    font-size: 12px;
    color: rgb(255, 255, 255);
    text-decoration: none;
    margin: 10px;
    text-transform: uppercase;
    padding: 8px 30px;
    box-shadow: 1px 2px 5px rgb(116, 116, 116);
    border-radius: 3px;
    transition: all .2s;
}

.Links a:hover {
    box-shadow: 2px 3px 10px rgb(116, 116, 116);

}

.Black {
    background: rgb(19, 19, 19);
}
a.Blue {
    color: black;
    background: rgb(255, 255, 255);
}